@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

.app {
    height: 100vh;
    display: flex;
    background-color: #020230;
    color:#fff;

    .main {
        width: 75%;
        background: linear-gradient(to bottom , rgba(0,0,0,0) , #020230) , url("../public/assets/bg.jpg") no-repeat ;
        display: flex;
        flex-direction: column;
        // position: relative;
        .top {
            height: 50%;
            position: relative;

            .timer {
                height: 50%;
                width: 80px;
                height: 80px;
                border: 5px solid white;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 30px;
                font-weight: bolder;
                position: absolute;
                bottom: 20px;
                left: 120px;
            }
        }

        .bottom {
            height: 50%;
            .quiz {
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                align-items: center;
            }
        }
    }

    .pyramid {
        width: 25%;
        display: flex;
        justify-content: center;
        align-items: center;

        .moneyList {
            list-style:none;
            padding: 20px;
            width: 100%;

            .moneyListItem {
                display: flex;
                align-items: center;
                padding: 8px 2px;
                border-radius: 8px;
                margin-bottom: 5px;
                &.active {
                    background-color: teal;
                }

                .moneyListItemNumber {
                    font-size: 18px;
                    font-weight: 200;
                    width: 30%;
                    margin-left: 5px;
                }

                .moneyListItemAmount {
                    font-size: 22px;
                    font-weight: 500;
                }
            }
        }
    }
}

