.question {
  background: -webkit-gradient(linear, left top, left bottom, from(#100241), to(black));
  background: linear-gradient(#100241, black);
  width: 80%;
  padding: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border-radius: 15px;
  font-size: 24px;
  font-weight: 500;
}

.answers {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.answers .answer {
  width: 40%;
  background: -webkit-gradient(linear, left top, left bottom, from(#0e0124), to(#22074d));
  background: linear-gradient(#0e0124, #22074d);
  padding: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 10px;
  color: white;
  font-size: 20px;
  margin: 0 10px 20px;
  font-weight: 700;
  cursor: pointer;
  -webkit-transition: hover ease 0.4s;
  transition: hover ease 0.4s;
}

.answers .answer:hover {
  background: mediumblue;
}

.answers .answer.active {
  background: mediumblue;
}

.answers .answer.correct {
  -webkit-animation: correct 3s ease-in-out forwards;
          animation: correct 3s ease-in-out forwards;
}

.answers .answer.wrong {
  -webkit-animation: wrong 3s ease-in-out forwards;
          animation: wrong 3s ease-in-out forwards;
}

@-webkit-keyframes correct {
  0%,
  22%,
  42%,
  62% {
    background: mediumblue;
  }
  20%, 40%, 60% {
    background: -webkit-gradient(linear, left top, left bottom, from(#0e0124), to(#22074d));
    background: linear-gradient(#0e0124, #22074d);
  }
  63%, 100% {
    background: green;
  }
}

@keyframes correct {
  0%,
  22%,
  42%,
  62% {
    background: mediumblue;
  }
  20%, 40%, 60% {
    background: -webkit-gradient(linear, left top, left bottom, from(#0e0124), to(#22074d));
    background: linear-gradient(#0e0124, #22074d);
  }
  63%, 100% {
    background: green;
  }
}

@-webkit-keyframes wrong {
  0%,
  22%,
  42%,
  62% {
    background: mediumblue;
  }
  20%, 40%, 60% {
    background: -webkit-gradient(linear, left top, left bottom, from(#0e0124), to(#22074d));
    background: linear-gradient(#0e0124, #22074d);
  }
  63%, 100% {
    background: crimson;
  }
}

@keyframes wrong {
  0%,
  22%,
  42%,
  62% {
    background: mediumblue;
  }
  20%, 40%, 60% {
    background: -webkit-gradient(linear, left top, left bottom, from(#0e0124), to(#22074d));
    background: linear-gradient(#0e0124, #22074d);
  }
  63%, 100% {
    background: crimson;
  }
}
