.question {
    background: linear-gradient(#100241 , black);
    width: 80%;
    padding: 20px;
    display: flex;
    justify-content: center;
    border-radius: 15px;
    font-size: 24px;
    font-weight: 500;
}

.answers {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    .answer {
        width: 40%;
        background: linear-gradient(#0e0124 , #22074d);
        padding: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius:10px;
        color:white;
        font-size: 20px;
        margin: 0 10px 20px; 
        font-weight: 700;
        cursor: pointer;
        transition: hover ease 0.4s;

        &:hover { 
            background: mediumblue;
        }

        &.active {
            background: mediumblue;
        }

        &.correct {
            animation:correct 3s ease-in-out forwards ;
        }

        &.wrong {
            animation :wrong 3s ease-in-out forwards ;
        }
    }
}

//! animation correct 
@keyframes correct {
    0% , 
    22% ,
    42% , 
    62%{
        background: mediumblue;
    }

    20% , 40% , 60%{
        background: linear-gradient(#0e0124 , #22074d);
    }

    63% , 100% {
        background: green;
    }
}

//! animation wrong 
@keyframes wrong {
    0% , 
    22% ,
    42% , 
    62%{
        background: mediumblue;
    }

    20% , 40% , 60%{
        background: linear-gradient(#0e0124 , #22074d);
    }

    63% , 100% {
        background: crimson;
    }
}