form {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    flex-direction: column;

    input {
        padding: 10px;
        border:none;
        outline:0 ;
        border:1px solid linear-gradient(#0e0124 , #22074d , teal);
        width: 20%;
        margin-bottom: 20px;
        border-radius: 5px;
        font-size: 18px;
        caret-color: teal;
        color:teal;
    }

    button {
        padding: 10px;
        font-size: 18px;
        width: max-content;
        background-color: teal;
        border:none;
        outline: 0 ;
        color:#fff; 
        font-weight: 600;
        border-radius:5px;
        cursor: pointer;
        transition: hover 0.5s ease-in;
        &:hover {
            background-color: #fff; 
            color:teal;
        }
    }
}