@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

.app {
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #020230;
  color: #fff;
}

.app .main {
  width: 75%;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(#020230)), url("../public/assets/bg.jpg") no-repeat;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #020230), url("../public/assets/bg.jpg") no-repeat;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.app .main .top {
  height: 50%;
  position: relative;
}

.app .main .top .timer {
  height: 50%;
  width: 80px;
  height: 80px;
  border: 5px solid white;
  border-radius: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 30px;
  font-weight: bolder;
  position: absolute;
  bottom: 20px;
  left: 120px;
}

.app .main .bottom {
  height: 50%;
}

.app .main .bottom .quiz {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.app .pyramid {
  width: 25%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.app .pyramid .moneyList {
  list-style: none;
  padding: 20px;
  width: 100%;
}

.app .pyramid .moneyList .moneyListItem {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 8px 2px;
  border-radius: 8px;
  margin-bottom: 5px;
}

.app .pyramid .moneyList .moneyListItem.active {
  background-color: teal;
}

.app .pyramid .moneyList .moneyListItem .moneyListItemNumber {
  font-size: 18px;
  font-weight: 200;
  width: 30%;
  margin-left: 5px;
}

.app .pyramid .moneyList .moneyListItem .moneyListItemAmount {
  font-size: 22px;
  font-weight: 500;
}
