form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

form input {
  padding: 10px;
  border: none;
  outline: 0;
  border: 1px solid linear-gradient(#0e0124, #22074d, teal);
  width: 20%;
  margin-bottom: 20px;
  border-radius: 5px;
  font-size: 18px;
  caret-color: teal;
  color: teal;
}

form button {
  padding: 10px;
  font-size: 18px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  background-color: teal;
  border: none;
  outline: 0;
  color: #fff;
  font-weight: 600;
  border-radius: 5px;
  cursor: pointer;
  -webkit-transition: hover 0.5s ease-in;
  transition: hover 0.5s ease-in;
}

form button:hover {
  background-color: #fff;
  color: teal;
}
